.container_contact {
  margin-top: 100px;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 100px;
}

.container_contact form {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-bottom: 20px;
}

.container_contact form fieldset {
  border: none;
  margin: 0px;
  padding: 0px;
  width: 50%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.container_contact form fieldset textarea {
  outline: inherit;
  margin-top: 20px;
}

.container_contact form fieldset button {
  outline: inherit;
