.mypicture{
    width: 90px;
    height: 90px;
    border-radius: 30px;
}

.linelink{
    width: 0%;
    height: 4px;
    background-color: #91B2FB;
}

.link:hover .linelink{
    -webkit-animation-name: mexe_prala_e_praca;
            animation-name: mexe_prala_e_praca;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    width: 100%;
}

@-webkit-keyframes mexe_prala_e_praca {
   0% { width: 0%;}
   100% { width: 100%;}
}

@keyframes mexe_prala_e_praca {
   0% { width: 0%;}
   100% { width: 100%;}
}
h3 {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 30px;
  margin-bottom: 20px;
}

